@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap);
*{box-sizing:border-box}html{background-color:#003844;color:#ffebc6;overflow-x:hidden}body{height:100vh;padding:12vmin 0;margin:0;font-family:"Roboto Mono", monospace, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body h2{font-family:"DotGothic16", sans-serif}#root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App{text-align:center}p{margin:0 0 1em;font-size:0.9rem}.about,.experiences,.projects{position:absolute;width:85%;z-index:1}@media (min-width: 500px){.about,.experiences,.projects{float:left;width:90%}}

.about__header{margin-top:0;margin-bottom:0}@media (min-width: 500px){.about__header{margin-top:0.83em}}.about__subheader{margin-top:20px;margin-bottom:10px;font-size:1rem}.about__timeline-item{padding-left:50px;text-indent:-50px}.about__timeline-year{font-weight:bold;margin-right:1em}.about__section-contact ul{margin-top:0;margin-bottom:10px}.about__section-contact li{font-size:0.9rem}.about__section-contact a{position:relative;list-style:none;color:#848fa5;text-decoration:none;transition:-webkit-clip-path 275ms ease;transition:clip-path 275ms ease;transition:clip-path 275ms ease, -webkit-clip-path 275ms ease}.about__section-contact a:hover::before{text-decoration:underline;-webkit-clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%);clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%)}.about__section-contact a::before{position:absolute;content:attr(data-content);color:#ffebc6;-webkit-clip-path:polygon(0 0, 0 0, 0% 100%, 0 100%);clip-path:polygon(0 0, 0 0, 0% 100%, 0 100%);transition:-webkit-clip-path 275ms ease;transition:clip-path 275ms ease;transition:clip-path 275ms ease, -webkit-clip-path 275ms ease}.adaptive-glass{--glass-lightness: 100%;background:hsl(0, 0%, var(--glass-lightness)/50%);-webkit-backdrop-filter:blur(40px);backdrop-filter:blur(40px)}@media (prefers-color-scheme: dark){.adaptive-glass{--glass-lightness: 0%}}@supports not ((-webkit-backdrop-filter: blur(1px)) or (backdrop-filter: blur(1px))){.adaptive-glass{background:hsl(0, 0%, var(--glass-lightness)/90%)}}

.experiences__nav{display:flex;flex-wrap:nowrap;list-style:none;overflow-x:auto;padding:0}.experiences::-webkit-scrollbar-thumb{background:#888}.experiences::-webkit-scrollbar-thumb:hover{background:#555}.experiences__item{white-space:nowrap}.experiences__button{cursor:pointer;border-style:none;border-bottom:2px solid #045388;background-color:transparent;color:#3498db;font-size:1rem;padding:0.6rem 0.8rem;color:grey}.experiences__button.button--active{color:#ffebc6;border-bottom:2px solid #9ed6fbd8;background-color:#004857}.experiences__button:focus{outline:none}.experiences__button:hover{background-color:#004857;color:#ffebc6;transition:background-color 300ms linear, color 300ms linear}

::-webkit-scrollbar{display:none}.experience>ul{text-align:justify;padding-left:1rem}.experience>ul>li{margin-top:1rem;font-size:0.9rem}@media (min-width: 768px){.experience>ul{max-width:550px}}

.layout__nav{width:100%}.layout__nav-items{list-style:none;justify-content:space-around;padding:5px;border-top:2px solid #fafafa1a}@media (min-width: 391px){.layout__nav-items{border-top:0}}

.layout__nav-item{position:relative;display:inline-block;width:-webkit-max-content;width:max-content}.layout__nav-item>.active{text-shadow:1px 1px 2px black}.layout__nav-item a{float:left;text-decoration:none;color:#b6b5b5;font-weight:bold;padding:0.6rem 0.8rem}.layout__nav-item a:hover,.layout__nav-item a:active,.layout__nav-item a.active{color:whitesmoke;text-shadow:1px 1px 10px black}@media (min-width: 500px){.layout__nav-item{margin-top:1.5px;display:flex;height:100%;width:auto;align-items:center}.layout__nav-item a{color:#b6b5b5;height:100%;padding:16px 10px;border-bottom:4px solid transparent;transition:all 150ms ease-out}.layout__nav-item a:hover{color:whitesmoke;-webkit-transform:translateY(-5px);transform:translateY(-5px);transition:-webkit-transform 150ms ease-out;transition:transform 150ms ease-out;transition:transform 150ms ease-out, -webkit-transform 150ms ease-out}.layout__nav-item a:active,.layout__nav-item a.active{color:whitesmoke;text-shadow:1px 1px 10px black}}

.layout{height:100%;position:relative;padding-left:15px;padding-right:15px;margin-right:auto;margin-left:auto;max-width:645px}.layout__header{text-align:center}@media (min-width: 391px){.layout__header{float:left}}.layout__header h1{font-size:16px;margin:0.6em 0}.layout__image{text-align:center;-webkit-filter:drop-shadow(3px 3px 3px #202020);filter:drop-shadow(3px 3px 3px #202020)}.layout__main{height:73vh;max-width:515px;background:transparent;border-radius:0.5ch;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);box-shadow:0 25px 50px rgba(0,0,0,0.2);padding:1rem;position:relative}@media (min-width: 500px){.layout__main{height:80vh;width:100%;position:absolute;left:10rem;float:left;padding:2rem}}.transition-enter,.transition-appear{opacity:0;z-index:1}.transition-enter-active,.transition-appear-active{opacity:1;transition:opacity 300ms linear 150ms}.transition-exit{opacity:1}.transition-exit.transition-exit-active{opacity:0;transition:opacity 300ms linear}.transition-exit-done{opacity:0}

.projects p{background:#008187;padding:0.5em 0 1em 1em;color:black;font-weight:bold;border-radius:0.3rem}.projects__tech-list{padding-left:0}.projects__tech-list li{display:inline-block;margin-right:2em}

